body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.div_padding_top{
  padding-top: 20px;
}
.div_padding_top h2{
  padding-bottom: 6px;
  border-bottom: 1px solid #d9d9d9;
}
.float_right{
  float: right;
}
/* .div_padding_top>Input{
  padding-bottom: 20px;
} */
.padd_rgt_10px{
  padding-right: 10px;
  padding-top: 20px;
}
.lightColor{
  color:#e8e4e4;
}
.subLists{
  list-style: none;
}

App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
.rbc-timeslot-group{
  min-height:30px;
}
.rbc-day-slot .rbc-event-content{
  font-size: 90%;
}
.rbc-event-label{
  font-size:60%;
}

.PrintPatientForm table {
    width: 100%;
    border-collapse: collapse;
}

.PrintPatientForm  body {
    font-family: Helvetica, Arial, sans-serif;
    font-size: 90%;
    width: 1024px;
}

.PrintPatientForm  h1,.PrintPatientForm  h2,.PrintPatientForm  h4 {
    margin: 10px 0;
}

.PrintPatientForm .centeralign {
    text-align: center;
}

.PrintPatientForm .rightalign {
    text-align: right;
}

.PrintPatientForm .strong {
    font-weight: bold;
}

.PrintPatientForm td {
    padding: 7px 20px;

}

.PrintPatientForm .grey_bk {
    background-color: #CFCFCF
}

.PrintPatientForm .textfield {
    width: 400px;
    height: 25px;
    float: left;
    border: 1px solid #848484;
    display: inline-block;
}

.PrintPatientForm .radio {
    width: 25px;
    height: 25px;
    float: left;
    border: 1px solid #848484;
    display: inline-block;
}

.PrintPatientForm label {
    float: left;
    line-height: 25px;
    margin-left: 5px;
    margin-right: 20px;
}

.PrintPatientForm .textarea {
    width: 400px;
    height: 100px;
    float: left;
    border: 1px solid #848484;
    display: inline-block;
}

.PrintPatientForm .signature {
    padding: 5px 30px;
    border-top: 1px solid #000;
    margin-top: 100px;
    margin-left: 50%;
    display: inline-block;
}

.PrintPatientForm .date {
    padding: 5px 20px;
    border-top: 1px solid #000;
    margin-top: 100px;
    display: inline-block;
}

.PrintPatientForm .leftrightmargin_10 {
    margin: 0px 10px;
}

.loginLayout{
  min-height:100vh;
}
.login-form-button{
  width:100%;
}
.socialLoginButton{
  background:transparent;

}
.submit-form-button{
  width:50%;
}

